.header {
  max-width: 880px;
  margin: 0 auto;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
  padding-top: 45px;
  padding-bottom: 41px;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width:879px) {
  .header {
    width: 100%;
    padding-top: 28px;
    padding-bottom: 31.6px;
  }
}
